$col-number: 12;
$space: 30px;

.row {
  display: flex;
  flex-wrap: wrap;
}

[class^="col-"] {
  padding-right: $space / 2;
  padding-left: $space / 2;
}

.col {
  flex: 1 0 0;
}

@for $i from 1 through $col-number {
  .col-#{$i} {
    width: (100% / $col-number) * $i;
  }
}
@for $i from 1 through $col-number {
  @include sm {
    .col-sm-#{$i} {
      width: (100% / $col-number) * $i;
    }
  }
}
@for $i from 1 through $col-number {
  @include md {
    .col-md-#{$i} {
      width: (100% / $col-number) * $i;
    }
  }
}
