$padding-sizes: (
    0: 0,
    10: 10px,
    70: 70px,
    'auto': auto
);

$margin-sizes: (
    0: 0px,
    10: 10px,
    40: 40px,
    'auto': auto

);

$directions: (
    't': top,
    'r': right,
    'b': bottom,
    'l': left
);

// Generating padding and margin classes

// Padding
@each $name, $size in $padding-sizes {
    @each $d-name, $d-value in $directions{
        
        .p-#{$name}{
            padding: $size !important;
        }
        .p#{$d-name}-#{$name}{
            padding-#{$d-value}: $size !important;
        }
        @include sm-down{
            .p-sm-#{$name}{
                padding: $size !important;
            }
        }
    }
    // X & Y
    .px-#{$name}{
        padding-left: $size !important;
        padding-right: $size !important;
    }
    // X & Y
    .py-#{$name}{
        padding-top: $size !important;
        padding-bottom: $size !important;
    }
}
// Margin
@each $name, $size in $margin-sizes {
    @each $d-name, $d-value in $directions{
        // .pt-10
        .m#{$d-name}-#{$name}{
            margin-#{$d-value}: $size;
        }
    }
    // X & Y
    .mx-#{$name}{
        margin-left: $size;
        margin-right: $size;
    }
    // X & Y
    .my-#{$name}{
        margin-top: $size;
        margin-bottom: $size;
    }
}

// Colors
$colors: (
    'white': #fff,
    'black': #000,
    'light': #FCFDFD,
    'dark-light': #F0F0F0,
    'dark': #3F3D42,
    'dark2': #281D3B,
    'dark3': #34234A,
    'blue': #1000FF,
    'sky': #91D5FF,
    'danger': #FF002A,
    'violet': #6B75F3,
    'github': #333,
    'google': #4285F4,
);

// ===================================
// ===================================
.d-flex{
    display: flex;
}
.d-block{
    display: block;
}

.list-unstyled{
    list-style: none;
    padding: 0px;
    margin: 0px;
}