.form{
    padding: 50px 30px;
    margin-top: 50px;
    overflow: hidden;
    background-color: color("light");
}

label{
    display: block;
    font-size: 14px;
    font-weight: bolder;
    color: color("dark");
}

.form-group{
    margin: 30px 0px;
    &:first-child{
        margin: 0px;
    }
}

input{
    display: block;
    width: 100%;
    margin: 10px auto;
    font-size: 18px;
    padding: 5px 10px;
    border: 1px solid color("sky");

    &.is-invalid{
        border-color: color("danger");
    }

}

.invalid-feedback{
    color: color("danger");
    display: none;
}

input.is-invalid + .invalid-feedback{
    display: block;
}

form{
    button{
        display: block;
        margin: 0px auto 20px auto;
        color: color("white");
        background-color: color("violet");
        padding: 10px 40px;
        font-size: 18px;
        border: none;
        cursor: pointer;
        &:hover{
            background-color: darken(color("violet"), 10);
        }
    }
}

.social-btn{
    display: block;
    width: 100%;
    padding: 10px 0px;
    margin: 20px auto;
    border: none;
    color: color("white");
    &:last-child{
        margin: 0px;
    }
}

.btn-google{
    background-color: color("google");
    &:hover{
        background-color: darken(color("google"), 10);
    }
}

.btn-github{
    background-color: color("github");
    &:hover{
        background-color: darken(color("github"), 10);
    }
}

.or{
    text-align: center;
    font-size: 18px;
    font-weight: bolder;
    color: color("dark");
    position: relative;
    &::before{
        content: "";
        display: block;
        width: 100%;
        height: 2px;
        background-color: color("dark");
        position: absolute;
        top: 50%;
        left: 0px;
        transform: translateY(-50%);
        z-index: 0;
    }
    span{
        background-color: color("light");
        padding: 0px 10px;
        z-index: 2;
        position: relative;
    }
}