.chat {
  height: calc(100vh - 70px);
  min-height: 500px;
  background-color: color("dark-light");
  overflow-y: auto;
  & > div {
    height: 100%;
  }
  .users {
    background-color: color("violet");
    overflow-y: auto;
  }
}
.chat-box {
  background-color: color("dark-light");
  padding: 0px 0px 15px 30px;
  display: flex;
  flex-direction: column;
  max-height: 100%;
  .messages {
    flex-grow: 1;
    overflow-y: auto;
    min-height: calc(100% - 250px);
  }
  @include sm-down {
    padding-left: 10px !important;
  }
}

.msg {
  @extend .col-md-6;
  background-color: color("white");
  color: color("dark");
  font-size: 16px;
  padding: 10px 20px 10px 10px;
  border-top-right-radius: 35px;
  border-bottom-right-radius: 35px;
  margin-bottom: 10px;
  margin-top: 10px;
  @include sm-down{
    // padding: 10px 20px 10px 10px;
  }
}

.msg-owner {
  @extend .col-md-6;
  @extend .ml-auto;
  background-color: color("violet");
  color: color("white");
  font-size: 16px;
  padding: 10px 10px 10px 20px;
  border-top-left-radius: 35px;
  border-bottom-left-radius: 35px;
  margin-bottom: 10px;
  margin-top: 10px;
}

.msg-form {
  position: relative;
  padding-right: 10px;
  textarea {
    font-size: 15px;
    color: color("dark");
    background-color: color("white");
    padding: 10px 40px 10px 20px;
    resize: none;
    display: block;
    width: 100%;
    border: none;
    border-radius: 35px;
    outline: none;
    max-height: 200px;
    &:focus {
      border: 2px solid color("sky");
    }
  }

  button {
    padding: 0px;
    margin: 0px;
    position: absolute;
    top: 5px;
    right: 20px;
    width: 27px;
    height: 27px;
    border-radius: 50%;
  }
}

.users-list {
  list-style: none;
  padding: 0px;
  margin: 0px;
  li {
    padding: 0px;
    margin: 0px;
  }
  a {
    color: color("white");
    display: inline-block;
    text-decoration: none;
    padding: 10px;
    font-size: 14px;
    width: 100%;
    &:hover {
      background-color: lighten(color("violet"), 10);
      color: lighten(color("white"), 10);
    }
    &.active {
      background-color: color("dark-light");
      color: color("violet");
    }
  }
}

.toggle {
  display: inline-block;
  margin-top: 3px;
  width: 21px;
  height: 21px;
  background-color: yellow;
  border-radius: 50%;
  cursor: pointer;
  position: relative;
  &::before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: -6px;
    background-color: #1aaafc;
    background-image: linear-gradient(to right, #23a1fb, #1ba5fb);
    border-radius: 50%;
    transition: 0.3s;
  }
}

body.dark {
  .navbar {
    background-color: color("dark3");
  }

  .chat-box {
    background-color: color("dark2");
  }

  .msg {
    background-color: color("dark3");
    color: color("white");
  }

  a {
    &.active {
      background-color: color("dark2");
      color: color("white");
    }
  }

  .toggle {
    &::before {
      left: -105%;
      background-color: transparent;
      background-image: none;
    }
  }
}
