.navbar {
  height: 70px;
  overflow: hidden;
    .row{
        height: 100%;
        align-items: center;
    }
}


.nav-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}


.navbar-nav {
  display: flex;
  align-items: center;
  list-style: none;
  padding: 0px;
  margin: 0px;
  font-size: 18px;
  font-weight: normal;
  color: color("white");
  li {
    margin: 0px 15px;
  }
  a {
    text-decoration: none;
    cursor: pointer;
    color: color("white");
    &:hover {
      color: lighten(color("blue"), 10);
    }
    &.active {
      color: color("blue");
      font-weight: bolder;
    }
  }
}

.nav-brand{
    font-size: 20px;
    font-weight: bolder;
}


// Header
header{
  color: color("white");
  text-align: center;
  h1{
    font-size: 64px;
    text-align: center;
    margin: 0px;
    padding: 0px;
  }

  @include md-down{
    h1{
      font-size: 32px;
    }
  }

  .btn{
    display: inline-block;
    background-color: color("white");
    color: color("dark");
    margin: 30px auto 0px auto;
    text-decoration: none;
    padding: 10px 60px;
    h2{
      margin: 0px;
      font-size: 24px;
    }
  }

}

.first-title{
  display: flex;
  justify-content: center;
}
.words{
 margin-left: 16px;
 height: 85px;
 overflow: hidden;
}

.words-list{
  display: flex;
  flex-direction: column;
  text-align: left;
  height: 100%;
}

.images{
  text-align: center;
}


#particles-js{
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  z-index: -1;
}

